* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.container {
  max-width: 1100px;
  margin: 0 auto;
}
.container--nav {
  display: grid;
  grid-template-columns: min-content 1fr;
  align-items: center;
}
.container--skills {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content min-content 1fr;
  grid-row-gap: 20px;
  align-content: center;
  justify-self: center;
}
.container--about-us {
  display: grid;
  grid-template-columns: 1fr 1fr min-content;
  grid-template-rows: min-content min-content;
  grid-template-areas: "lf lf hs" "tf tf hs" "pl pl hs";
  grid-column-gap: 50px;
  grid-row-gap: 20px;
}
@media (max-width: 900px) {
  .container--about-us {
    grid-template-areas: "hs hs hs" "lf lf lf" "tf tf tf" "pl pl pl";
  }
}
.container--systems {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 30px;
}
.body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #ffffff;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
}
.body--scrolloff {
  overflow: hidden;
}
.header {
  width: 100%;
  background-color: transparent;
}
.header__menu-bg--open {
  z-index: 1;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}
.header__wrap {
  width: 100%;
  height: 90px;
  z-index: 2;
  padding: 0 20px;
  position: fixed;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid #a1a1a1;
  transition: 0.5s all ease-in;
}
.header__wrap--scroll {
  background-color: rgba(255, 255, 255, 0.95);
  transition: 0.5s all ease-in;
  color: #000;
}
.header__logo {
  z-index: 2;
}
.main {
  width: 100%;
  height: 100%;
}
.footer {
  width: 100%;
  padding: 50px 20px;
  background: #F6F6F8;
}
.footer__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 20px;
}
@media (max-width: 600px) {
  .footer__wrap {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
}
.footer__info {
  max-width: 255px;
}
.footer__info--map {
  flex-grow: 1;
  max-width: none;
  width: 100%;
  height: 250px;
  background-image: url('../../images/footer__map-bg.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.footer__map {
  width: 100%;
  height: 250px;
}
.footer__title {
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #000000;
  margin-bottom: 10px;
}
@media (max-width: 900px) {
  .footer__title {
    text-align: center;
  }
}
.footer__sub-title {
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  color: #000000;
  margin-bottom: 10px;
}
@media (max-width: 900px) {
  .footer__sub-title {
    text-align: center;
  }
}
.footer__adress-icon {
  height: 15px;
  width: 15px;
  margin-left: 4px;
}
.footer__adress-link {
  text-decoration: underline;
  cursor: pointer;
}
.footer__adress-link:hover,
.footer__adress-link:focus {
  color: #000000;
}
.footer__text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  color: #999999;
}
.footer__text--legal-info {
  margin-top: 8px ;
}
@media (max-width: 900px) {
  .footer__text {
    text-align: center;
  }
}
.footer__rights {
  width: 100%;
  height: 100%;
  flex-grow: 1;
  padding: 20px 20px;
  margin-top: 20px;
  border-top: 1px solid #E0E0E0;
  text-align: center;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 21px;
  color: #999999;
}
.nav {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(6, auto);
  justify-content: end;
  grid-column-gap: 40px;
  background-color: transparent;
}
@media (max-width: 900px) {
  .nav {
    display: none;
  }
}
.nav--open {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: min-content;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(6, auto);
  grid-row-gap: 20px;
  padding: 90px 0 40px 0;
  border-bottom: 1px solid #000;
  background-color: #ffffff;
  color: #000000;
  box-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
}
.nav--open ~ .nav-button .ham .line {
  color: #000;
}
.nav__item {
  height: 100%;
  list-style: none;
}
.nav__link {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Rubik', sans-serif;
  color: currentColor;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
}
.nav__link--current {
  pointer-events: none;
}
.nav__link:hover::after,
.nav__link:focus::after,
.nav__link--current::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 107px;
  height: 2px;
  background-color: currentColor;
}
.nav-button {
  display: none;
  width: min-content;
  height: min-content;
  border: none;
  background-color: transparent;
}
@media (max-width: 900px) {
  .nav-button {
    z-index: 2;
    display: block;
    justify-self: end;
  }
}
.ham {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.ham--black > .line {
  stroke: #000;
}
.line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #fff;
  stroke-width: 5.5;
  stroke-linecap: round;
}
.hamRotate.active {
  transform: rotate(45deg);
}
.hamRotate.active > .line {
  stroke: #000;
}
.ham8 .top {
  stroke-dasharray: 40 160;
}
.ham8 .middle {
  stroke-dasharray: 40 142;
  transform-origin: 50%;
  transition: transform 400ms;
}
.ham8 .bottom {
  stroke-dasharray: 40 85;
  transform-origin: 50%;
  transition: transform 400ms, stroke-dashoffset 400ms;
}
.ham8.active .top {
  stroke-dashoffset: -64px;
}
.ham8.active .middle {
  transform: rotate(90deg);
}
.ham8.active .bottom {
  stroke-dashoffset: -64px;
}
.cover {
  width: 100%;
  height: 100vh;
  min-height: 600px;
  padding: 80px 20px;
  background-image: url('../images/cover.jpg');
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
}
.cover__title {
  font-family: 'Rubik', sans-serif;
  font-weight: 800;
  font-size: 48px;
  line-height: 64px;
  text-align: center;
  color: #ffffff;
}
@media (max-width: 600px) {
  .cover__title {
    font-weight: 600;
    font-size: 38px;
    line-height: 44px;
  }
}
.cover__link {
  width: 200px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #0068ab;
  box-shadow: 0px 0px 10px rgba(111, 111, 111, 0.25);
  border-radius: 30px;
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
}
.cover__link:hover,
.cover__link:focus {
  transition: 0.3s all ease-in;
  background-color: #0087de;
  box-shadow: 8px 8px 20px #bebebe, -8px -8px 20px #ffffff;
}
.skills {
  width: 100%;
  padding: 100px 20px;
}
@media (max-width: 900px) {
  .skills {
    padding: 30px 20px;
  }
}
.skills__title {
  font-family: 'Rubik', sans-serif;
  font-weight: bold;
  font-size: 36px;
  line-height: 53px;
  text-align: center;
  color: #323232;
}
@media (max-width: 900px) {
  .skills__title {
    font-size: 30px;
    line-height: 33px;
  }
}
.skills__subtitle {
  font-family: 'Rubik', sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  text-align: center;
  color: #999999;
}
.skills__list {
  width: 100%;
  padding: 50px 0 0 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 50px;
}
@media (max-width: 900px) {
  .skills__list {
    padding: 10px 0 0 0;
  }
}
.skills__item {
  width: 200px;
  position: relative;
  list-style: none;
  display: grid;
  grid-template-columns: min-content 1fr;
  grid-template-rows: repeat(2, min-content);
  grid-column-gap: 15px;
  grid-row-gap: 5px;
}
.skills__skill {
  font-family: 'Rubik', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #000000;
}
.skills__text {
  font-family: 'Rubik', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  color: #999999;
  grid-column-start: 1;
  grid-column-end: -1;
}
.about-us {
  width: 100%;
  padding: 100px 20px;
  background: #F6F6F8;
}
@media (max-width: 900px) {
  .about-us {
    padding: 50px 20px;
  }
}
.about-us__info {
  position: relative;
  grid-area: lf;
  height: min-content;
  display: grid;
  grid-template-rows: min-content min-content;
  grid-row-gap: 20px;
}
.about-us__history {
  grid-area: hs;
  display: grid;
  grid-template-rows: repeat(3, min-content);
  grid-row-gap: 20px;
  padding: 40px 20px;
  background-color: #0068ab;
  color: #ffffff;
  box-shadow: 14px 14px 28px #929292, -14px -14px 28px #f5f5f5;
  border-radius: 30px;
}
@media (max-width: 900px) {
  .about-us__history {
    padding: 20px;
    grid-template-areas: "tl tl tl" "tx tx im";
    grid-row-gap: 10px;
    grid-column-gap: 10px;
  }
}
@media (max-width: 900px) {
  .about-us__history {
    display: block;
  }
}
.about-us__mission {
  grid-area: tf;
}
.about-us__politic {
  grid-area: pl;
}
.about-us__title {
  font-family: 'Rubik', sans-serif;
  font-weight: bold;
  font-size: 36px;
  line-height: 53px;
  color: #323232;
}
.about-us__title--history {
  color: #ffffff;
}
@media (max-width: 900px) {
  .about-us__title--history {
    grid-area: tl;
  }
}
.about-us__text {
  font-family: 'Rubik', sans-serif;
  font-size: 16px;
  line-height: 25px;
}
.about-us__img {
  width: 200px;
  height: 200px;
  background-color: #929292;
}
@media (max-width: 900px) {
  .about-us__img {
    grid-area: im;
  }
}
@media (max-width: 900px) {
  .about-us__img {
    width: 150px;
    height: 150px;
    float: left;
    margin: 0 8px 0 0;
  }
}
.about-us__quotes {
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
}
@media (max-width: 900px) {
  .about-us__quotes {
    grid-area: tx;
  }
}
.about-us__quotes-autor {
  font-family: 'Open Sans', sans-serif;
  text-transform: initial;
  display: block ;
  margin-top: 10px;
}
.mission-list {
  padding-left: 20px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content;
  grid-row-gap: 10px;
}
.mission-list__item {
  font-family: 'Rubik', sans-serif;
}
.systems {
  width: 100%;
  padding: 40px 20px;
  background: #0068ab;
  background: linear-gradient(180deg, #0068ab 0%, rgba(18, 76, 208, 0.54665616) 46%, rgba(196, 196, 196, 0.263743) 46%, #ffffff 65%);
}
@media (max-width: 900px) {
  .systems {
    padding: 20px 10px 40px 10px;
    background: linear-gradient(180deg, #0068ab 0px, rgba(18, 76, 208, 0.54665616) 83px, rgba(196, 196, 196, 0.263743) 104px, #ffffff 15%);
  }
}
.systems__titles {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
@media (max-width: 900px) {
  .systems__titles {
    justify-content: center;
  }
}
.systems__title {
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 53px;
  color: #ffffff;
  font-family: 'Open Sans', sans-serif;
}
@media (max-width: 900px) {
  .systems__title {
    margin-bottom: 20px;
  }
}
.sys-list {
  width: 100%;
  padding: 0 0 10px 0;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  gap: 20px;
  list-style: none;
  border-bottom: 1px solid #E0E0E0;
}
@media (max-width: 900px) {
  .sys-list {
    flex-direction: column;
    border-bottom: none;
  }
}
.sys-list__item {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, min-content);
  grid-row-gap: 15px;
  width: min-content;
  margin: 10px;
  border-radius: 13px;
}
@media (max-width: 900px) {
  .sys-list__item {
    width: 100%;
    margin: 0;
    grid-template-columns: repeat(2, min-content);
    grid-column-gap: 20px;
    grid-template-areas: "img img title title" "img img text text" "img img link link";
  }
}
.sys-list__img {
  display: flex;
  width: 100%;
  height: 100%;
  object-fit: cover;
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
.sys-list__image-link {
  border-radius: 13px;
  border-top: 2px solid #fff;
  border-left: 2px solid #fff;
  border-right: 2px solid #fff;
  overflow: hidden;
  width: 250px;
  height: 180px;
}
.sys-list__image-link:hover .sys-list__img,
.sys-list__image-link:focus .sys-list__img {
  -webkit-transform: scale(1);
  transform: scale(1);
}
.sys-list__image-link:hover,
.sys-list__image-link:focus {
  box-shadow: 5px 5px 18px #bebebe, -5px -5px 18px #ffffff;
  transition: 0.4s ease-in-out;
}
@media (max-width: 900px) {
  .sys-list__image-link {
    grid-area: img;
  }
}
@media (max-width: 600px) {
  .sys-list__image-link {
    width: 160px;
    height: 160px;
  }
}
.sys-list__title {
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  font-family: 'Open Sans', sans-serif;
}
@media (max-width: 900px) {
  .sys-list__title {
    grid-area: title;
  }
}
.sys-list__text {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  color: #999999;
  font-family: 'Rubik', sans-serif;
}
@media (max-width: 900px) {
  .sys-list__text {
    grid-area: text;
  }
}
.sys-list__link {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  font-family: 'Open Sans', sans-serif;
  text-decoration-line: underline;
  color: #000000;
}
.sys-list__link:hover,
.sys-list__link:focus {
  color: #0068ab;
}
@media (max-width: 900px) {
  .sys-list__link {
    grid-area: link;
  }
}
.reference {
  width: 100%;
  background-color: #ffffff;
  background-image: url('../images/reference__background-img--foo.jpg');
  backdrop-filter: blur(5px);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.reference--wrap {
  padding: 80px 20px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr;
  grid-row-gap: 40px;
}
.reference__title {
  font-weight: bold;
  font-size: 36px;
  line-height: 53px;
  text-align: center;
  font-family: 'Rubik', sans-serif;
  color: #ffffff;
}
.reference__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 15px;
  grid-row-gap: 15px;
  list-style: none;
}
@media (max-width: 900px) {
  .reference__list {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, min-content);
  }
}
@media (max-width: 600px) {
  .reference__list {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, min-content);
  }
}
.reference__item {
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-end;
  gap: 10px;
  padding: 20px 5px;
  background-color: rgba(0, 0, 0, 0.6);
}
.reference__firm {
  order: 1;
  font-family: 'Rubik', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 25px;
  color: #ffffff;
}
.reference__text {
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  text-align: center;
  color: #ffffff;
}
.reference__logo {
  align-self: center;
  width: 140px;
  height: 80px;
  object-fit: contain;
  background-color: transparent;
  display: flex;
}
