* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  //font-family: "PT Sans", "Arial", sans-serif;
}

@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.container {
  max-width: @desctope-width;
  margin: 0 auto;

  &--nav {
    display: grid;
    grid-template-columns: min-content 1fr;
    align-items: center;
  }

  &--skills {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: min-content min-content 1fr;
    grid-row-gap: 20px;
    align-content: center;
    justify-self: center;
  }

  &--about-us {
    display: grid;
    grid-template-columns: 1fr 1fr min-content;
    grid-template-rows: min-content min-content;
    grid-template-areas:
      "lf lf hs"
      "tf tf hs"
      "pl pl hs"
    ;
    grid-column-gap: 50px;
    grid-row-gap: 20px;

    @media (max-width: @tablet-width) {

      grid-template-areas:
      "hs hs hs"
      "lf lf lf"
      "tf tf tf"
      "pl pl pl"
    ;

    }
  }

  &--systems {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 30px;
  }
}

.body {
  min-height: 100vh;

  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: @standart-white;

  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;

  &--scrolloff {
    overflow: hidden;
  }
}
